<template>
  <div class="container home">
    <b-row class="container my-3">
      <b-button 
        size="md" 
        @click="refresh"
        variant="dark"
      >
        Perbaharui 
      </b-button>
    </b-row>
    <h6>Pesanan</h6>
    <div class="mt-3 scrollable" id="list-material">
      <b-table
        striped
        hover
        fixed
        filter="0"
        :filter-included-fields="order_status"
        :fields="order_fields"
        :items="order_items"
        head-variant="dark"
      >
        <template #cell(action)="row">
          <b-button 
            size="sm" 
            @click="to_modal_click(row.item.status_now,1,row.item.po_code,'weigh_modal_ref')"
            variant="primary"
          >
            Timbang 
          </b-button>
        </template>
      </b-table>

      <b-modal
        ref="weigh_modal_ref"
        centered
        hide-header
        hide-footer
        id="weigh_modal"
      >
        <h4> Apakah ingin mencetak QR Code dan memulai penimbangan? </h4>
        <br/>
        <br/>
        <br/>
        <br/>
        <b-button class="m-2" variant="primary" @click="action_click(sel_status_now,sel_chg,sel_po_code,true, 'weigh_modal_ref')">
          Cetak dan Timbang
        </b-button>
        <b-button class="m-2" variant="secondary" @click="action_click(sel_status_now,sel_chg,sel_po_code,false, 'weigh_modal_ref')">
          Timbang
        </b-button>
        <b-button class="m-2" variant="danger" @click="action_click(-99,0,sel_po_code,false, 'weigh_modal_ref')">
          Hapus
        </b-button>
        <b-button class="m-2" variant="dark" @click="hide_modal('weigh_modal_ref')">
          Tidak
        </b-button>
      </b-modal>
    </div>
    <hr/>
    <h6>Sedang Ditimbang</h6>
    <div class="mt-3 scrollable" id="list-material">
      <b-table
        striped
        hover
        fixed
        filter="1"
        :filter-included-fields="order_status"
        :fields="order_fields"
        :items="order_items"
        head-variant="dark"
      >
        <template #cell(action)="row">
          <b-button 
            size="sm" 
            @click="to_modal_click(row.item.status_now,-1,row.item.po_code, 'cancel_modal_ref')"
            variant="danger"
          >
            Batal 
          </b-button>
        </template>
      </b-table>
      <b-modal
        ref="cancel_modal_ref"
        centered
        hide-header
        hide-footer
        id="cancel_modal"
      >
        <h4> Apakah Anda yakin? </h4>
        <br/>
        <br/>
        <br/>
        <br/>
        <b-button class="m-2" variant="primary" @click="action_click(sel_status_now,sel_chg,sel_po_code,false, 'cancel_modal_ref')">
          Ya
        </b-button>
        <b-button class="m-2" variant="danger" @click="hide_modal('cancel_modal_ref')">
          Tidak
        </b-button>
      </b-modal>
    </div>
    <hr/>
    <h6>Sudah Selesai</h6>
    <div class="mt-3 scrollable" id="list-material">
      <b-table
        striped
        hover
        fixed
        filter="2"
        :filter-included-fields="order_status"
        :fields="order_fields"
        :items="order_items"
        head-variant="dark"
      >
        <template #cell(action)="row">
          <b-button 
            size="sm" 
            
            @click="to_modal_click(row.item.status_now,1,row.item.po_code, 'cancel_modal_ref')"
            variant="primary"
          >
            Selesai 
          </b-button>
        </template>
      </b-table>
    </div>
  </div>
</template>

<script> 
import axios from "axios";

export default {
  name: "CoordMain",
  components: {
  },
  data(){
    return{
      sel_status_now:0,
      sel_chg:0,
      sel_po_code:0, 
      sel_print:false,
      order_status:["status_now"],
      order_fields: [
        { key: 'po_code', label: 'Po' },
        { key: 'feed_code', label: 'Feed' },
        { key: 'batch_num', label: 'Jmlh Batch' },
        { key: 'priority_num', label: 'Prioritas' },
        { key: 'checklist', label: 'Cek' },
        { key: 'action', label: 'Aksi' },
      ],
      order_items: [],
    }
  },
  methods:{
    hide_modal(modal_ref) {
      this.$refs[modal_ref].hide();
    },
    to_modal_click(val,chg,code,modal_ref){
      this.sel_status_now=val;
      this.sel_chg=chg;
      this.sel_po_code=code;
      this.$refs[modal_ref].show();
    },
    action_click(val, chg, code, print, modal_ref){
      let status_now = parseInt(val)+chg;
      let row = this.order_items.map((o) => o.po_code).findIndex((val) => val === code);
      let num = 0;
      if (row !== -1) num = this.order_items[row].batch_num;

      if (parseInt(val) === 0){
        if(print){
          axios
            .post(`/api/printer`, 
            {
              timeout: 5000,
              po_code : code,
              batch_num: num
            })
            .then((res) => {
              console.log(res);
              axios
                .put(`/api/listpo/update`, 
                {
                  timeout: 5000,
                  status : status_now,
                  po_code : code
                })
                .then((res) => {
                  console.log(res)
                })
                .catch((err) => {
                  console.error(err);
                });
            })
            .catch((err) => {
              console.error(err);
            });
        }
        else{
          axios
            .put(`/api/listpo/update`, 
            {
              timeout: 5000,
              status : status_now,
              po_code : code
            })
            .then((res) => {
              console.log(res)
            })
            .catch((err) => {
              console.error(err);
            });
        }
      }
      else{
        axios
          .put(`/api/listpo/update`, 
          {
            timeout: 5000,
            status : status_now,
            po_code : code
          })
          .then((res) => {
            console.log(res)
          })
          .catch((err) => {
            console.error(err);
          });
      }

      
      if (row !== -1) this.order_items[row].status_now = status_now;

      this.$refs[modal_ref].hide();
    },
    refresh(){
      axios
      .get(`/api/listpo`, {
        timeout: 5000,
      })
      .then((res) => {
        if (res.data.length > 0) {
          this.order_items = res.data;
        } 
      })
      .catch((err) => {
        console.error(err);
      });
    }
  }
};
</script>
